    height: 100vh;
}
.carousel-indicators button:not(.active){
    background-color: white;
    border-radius: 50%;
    height: 25px;
    width: 25px;
}
.carousel-indicators button.active{
    background-color: #F23229;
    border-radius: 50%;
    height: 25px;
    width: 25px;
}
.carousel-item-area .carousel-description{
    display: -webkit-flex;
