    font: 27px 'Montserrat', sans-serif !important;
    color: white;
}
.carousel-item-area .carousel-description button{
    font: 27px 'Sigmar One', sans-serif !important;
    background-color: #F28F05;
    color: white;
    font-size: 24.4px;
    width: 100%;
    border-radius: 15px;
}


