.integrant-section{
    margin: 15px;
    box-shadow: 0px 0px 17px 0px rgba(0,0,0,0.41);
    border-radius: 2px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
