.left-tab-menu {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 20vw;
    height: 100vh;
    background-color: white;
    border-right: 1px solid #8A8A8A;
}

.left-tab-menu h1{
    font: 18px 'Sigmar One', sans-serif !important;
    color: #DE4E8D;
    margin: 5px 0 15px 20px;
    text-align: left;
    height: auto;
}

.left-tab-menu .tab-button {
    padding: auto;
    cursor: pointer;
    font: 14px 'Sigmar One', sans-serif !important;
    text-align: center;
    color: white;
    border-radius: 15px;
    width: 240px;
    border: 1px solid #F23229;
    margin-bottom: 15px;
}

.left-tab-menu .tab-button:not(.active){
    background-color: white;
    color: #797979;
}

.left-tab-menu .tab-button:is(.active){
    background-color: #F28F05;
    color: white;
}

